import React from 'react'
import { Button, Container, Image, Info, Title } from '../Theme/CategoriesItems'

const CategoryItem  = ({items}) => {
    return (
        <Container>
            <Image  src={items.img} />
            <Info>
                <Title>{items.title}</Title>
                <Button>SHOP NOW</Button>
            </Info>
        </Container>
    )
}

export default CategoryItem 
